:root {
  --headline: 40px;
  --subtitle: 24px;
  --text--: 18px;
  --headline-color: lightblue;
  --nav-width: 90%;
  --nav-max-width: 1640px;
  --primary-theme-for-app: #f76835;
  --text-color-light-mode: white;
}

/* navbar css */
.bg-body-tertiary {
  background-color: var(--primary-theme-for-app) !important;
}
.nav-main-container {
  width: var(--nav-width);
  max-width: var(--nav-max-width);
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar-brand {
  color: black;
  font-size: var(--headline);
  font-weight: 600;
}
.navbar-nav .nav-link {
  color: var(--text-color-light-mode) !important;
  font-size: 20px;
}
.navbar-nav .nav-link:active {
  color: rgb(73, 73, 73) !important;
}
